#footer {
  font-family: $font-family;
  color: $color-red;
  background-color: #FAE1D2;
  .webCenter {
    width: 80%;
    display: flex;
    margin: 0 auto;
    .footer__info {
      width: 100%;
      height: 10rem;
      @include flexX(space-between);
      .info__facebook {
        @include flexX(center);
        color: $color-red;
        background-color: $color-yellow;
        height: 100px;
        width: 200px; }
      .info__webname {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        .webname__logo {
          cursor: pointer;
          height: 4rem;
          width: 4rem;
          border-radius: 50%; }
        .webname__english {
          margin: 0.5rem 0.75rem;
          color: $color-red;
          display: flex;
          align-items: center; } } } }
  .copyright {
    @include flexX(center);
    font-size: 1rem;
    color: $color-white;
    height: 2rem;
    background-color: #E6756B; } }
